import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router-dom";
import { eovList, progressCodes, roleCodes } from "../isoCodeLists";
import { Grid, Typography, Button, Paper } from "@material-ui/core";

import BilingualTextInput from "./BilingualTextInput";
import CheckBoxList from "./CheckBoxList";
import SelectInput from "./SelectInput";
import DateInput from "./DateInput";
import firebase from "../firebase";
import { auth } from "../auth";

import { En, Fr, I18n } from "./I18n";

const styles = {
  paper: {
    padding: "10px",
    margin: "20px",
  },
};
function camelToSentenceCase(text) {
  var result = text.replace(/([A-Z])/g, " $1");
  return result.charAt(0).toUpperCase() + result.slice(1);
}

class MetadataForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: { en: "", fr: "" },
      abstract: { en: "", fr: "" },
      id: "",
      eov: [],
      role: "",
      progress: "",
      dateStart: new Date(),
      recordID: "",
    };
  }

  handleInputChange = (event) => {
    const { name, value } = event.target;

    this.setState((state) => ({ ...state, [name]: value }));
  };
  async handleSubmitClick() {
    const rootRef = firebase
      .database()
      .ref(`test/users`)
      .child(auth.currentUser.uid);
    const { recordID, ...updateValues } = this.state;
    if (this.state.recordID) {
      await rootRef.child(recordID).update(updateValues);
    } else {
      await rootRef.push(updateValues);
      this.props.history.push(
        "/" + this.props.match.params.language + "/success"
      );
    }
  }
  firebaseToJSObject(json) {
    Object.keys(json).forEach((key) => {
      if (typeof json[key] === "object" && Object.keys(json[key])[0] === "0") {
        json[key] = Object.entries(json[key]).map(([k, v]) => v);
      }
    });
  }
  componentDidMount() {
    if (auth.currentUser) {
      let recordID;

      const { location } = this.props;
      if (location) {
        recordID =
          location.pathname.slice(1).includes("/") &&
          location.pathname.split("/").pop();
        if (recordID) {
          const databaseCallback = (records) => {
            const recordsObj = records.toJSON();

            if (recordsObj[recordID]) {
              const thisRecord = recordsObj[recordID];
              this.firebaseToJSObject(thisRecord);
              this.setState({ recordID, ...thisRecord });
            }
          };
          firebase
            .database()
            .ref(`test/users`)
            .child(auth.currentUser.uid)
            .on("value", databaseCallback);
        }
      }
    }
  }
  render() {
    return (
      <Grid
        container
        direction="column"
        justify="space-between"
        alignItems="stretch"
      >
        <Paper className={this.props.classes.paper}>
          <Typography variant="h3">
            <En>CIOOS Metadata Profile Intake Form</En>
            <Fr>Formulaire de réception des profils de métadonnées du CIOOS</Fr>
          </Typography>
          <Typography>
            <En>
              Welcome to the CIOOS metadata profile generation form! Please fill
              out each field with as much detail as you can. Using this
              information we will create your metadata profile for the given
              dataset.
            </En>
            <Fr>
              Bienvenue sur le formulaire de génération de profils de
              métadonnées CIOOS ! Veuillez remplir sur chaque champ avec autant
              de détails que vous pouvez. Utilisation de cette informations que
              nous allons créer votre profil de métadonnées pour le jeu de
              données.
            </Fr>
          </Typography>
        </Paper>
        <Paper className={this.props.classes.paper}>
          <Typography>
            <En>What is the dataset title?</En>
            <Fr>Quel est le titre du jeu de données?</Fr>
          </Typography>
          <BilingualTextInput
            name="title"
            value={this.state.title}
            onChange={this.handleInputChange}
            required
          />
        </Paper>

        <Paper className={this.props.classes.paper}>
          <Typography>
            <En>What is their role?</En>
            <Fr>Quel est leur rôle ?</Fr>
          </Typography>
          <SelectInput
            label={<I18n en="Choose" fr="Choisir" />}
            name="role"
            value={this.state.role}
            onChange={(e) => this.handleInputChange(e)}
            options={roleCodes}
            optionLabels={roleCodes.map(camelToSentenceCase)}
          />
        </Paper>
        <Paper className={this.props.classes.paper}>
          <Typography>
            <En>
              Please select all the essential ocean variables that are contained
              in this dataset
            </En>
            <Fr>
              Veuillez sélectionner toutes les variables océaniques essentielles
              contenues dans cet ensemble de données
            </Fr>
          </Typography>
          <CheckBoxList
            name="eov"
            value={this.state.eov}
            onChange={this.handleInputChange}
            options={eovList}
            optionLabels={eovList.map(camelToSentenceCase)}
          />
        </Paper>
        <Paper className={this.props.classes.paper}>
          <Typography>
            <En>What is the status of this dataset?</En>
            <Fr>Quel est l'état de ce jeu de données ?</Fr>
          </Typography>
          <SelectInput
            label={<I18n en="Choose" fr="Choisir" />}
            name="progress"
            value={this.state.progress}
            onChange={(e) => this.handleInputChange(e)}
            options={progressCodes}
            optionLabels={progressCodes.map(camelToSentenceCase)}
          />
        </Paper>

        <Paper className={this.props.classes.paper}>
          <Typography>
            <En>What is the dataset abstract?</En>
            <Fr>Qu'est-ce que le résumé du jeu de données ?</Fr>
          </Typography>
          <BilingualTextInput
            name="abstract"
            value={this.state.abstract}
            onChange={this.handleInputChange}
            multiline
          />
        </Paper>

        <Paper className={this.props.classes.paper}>
          <Typography>
            <En>What is the start date when data was first collected?</En>
            <Fr>
              Quelle est la date de début à laquelle les données ont été
              collectées pour la première fois?
            </Fr>
          </Typography>
          <DateInput
            name="dateStart"
            value={this.state.dateStart}
            onChange={this.handleInputChange}
          />
        </Paper>

        <Button
          variant="contained"
          color="primary"
          onClick={() => this.handleSubmitClick()}
        >
          {this.state.recordID ? (
            <I18n en="Update" fr="Mise à jour" />
          ) : (
            <I18n en="Submit" fr="Soumettre" />
          )}
        </Button>
      </Grid>
    );
  }
}

export default withRouter(withStyles(styles)(MetadataForm));
