import React from "react";
import { TextField, InputAdornment } from "@material-ui/core";
import { useParams } from "react-router-dom";

const BilingualTextInput = ({ onChange, value, name, multiline }) => {
  function handleChange(e) {
    const newData = { ...value, [e.target.name]: e.target.value };
    onChange({ target: { name, value: newData } });
  }
  const { language } = useParams();
  let languages;

  if (language === "en") languages = ["en", "fr"];
  else languages = ["fr", "en"];

  return (
    <div>
      {languages.map((lang) => (
        <div key={lang}>
          <TextField
            name={lang}
            fullWidth
            value={value && value[lang]}
            onChange={handleChange}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  {lang.toUpperCase()}
                </InputAdornment>
              ),
            }}
            multiline={multiline}
          />
        </div>
      ))}
    </div>
  );
};
const areEqual = (prevProps, nextProps) => prevProps.value === nextProps.value;
export default React.memo(BilingualTextInput, areEqual);
