import React from "react";
import firebase from "../firebase";
import { auth } from "../auth";
import {
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";

import { Delete, Edit } from "@material-ui/icons";
import { Fr, En } from "./I18n";

class Submissions extends React.Component {
  constructor(props) {
    super(props);
    this.state = { records: {}, modalOpen: false };
  }

  databaseCallback = (records) =>
    this.setState((state) => ({ ...state, records: records.toJSON() }));

  async componentDidMount() {
    if (auth.currentUser) {
      firebase
        .database()
        .ref(`test/users`)
        .child(auth.currentUser.uid)
        .on("value", this.databaseCallback);
    }
  }

  editRecord(key) {
    this.props.history.push(
      "/" + this.props.match.params.language + `/new/${key}`
    );
  }

  showModal() {
    this.setState((state) => ({ ...state, modalOpen: true }));
  }

  hideModal() {
    this.setState((state) => ({ ...state, modalOpen: false }));
  }

  deleteRecord(key) {
    if (auth.currentUser) {
      firebase
        .database()
        .ref(`test/users`)
        .child(auth.currentUser.uid)
        .child(key)
        .remove();
    }
  }
  render() {
    const { language } = this.props.match.params;

    return (
      <div>
        <Typography variant="h3">
          <En>Submission list</En>
          <Fr>Liste des soumissions</Fr>
        </Typography>
        {this.state.records && Object.keys(this.state.records).length > 0 ? (
          <div>
            <Typography>
              <En>These are the submissions we have received:</En>
              <Fr>Ce sont les soumissions que nous avons reçues</Fr>
            </Typography>
            <List>
              {Object.entries(this.state.records).map(([key, val]) => (
                <ListItem button key={key}>
                  <ListItemText primary={val.title[language]} />
                  <ListItemIcon onClick={() => this.editRecord(key)}>
                    <Edit />
                  </ListItemIcon>
                  <ListItemIcon onClick={() => this.deleteRecord(key)}>
                    <Delete />
                  </ListItemIcon>
                </ListItem>
              ))}
            </List>
          </div>
        ) : (
          <Typography>
            <En>No records submitted yet!</En>
            <Fr>Aucun enregistrement n'a encore été soumis</Fr>
          </Typography>
        )}
      </div>
    );
  }
}

export default Submissions;
